<template>
<div>
  <div class="title">
    <div class="mainTitle">库位使用情况</div>
    <div class="subTitle">全部占用面积</div>
  </div>
  <chart class="chart" :options="option" ref="chart" style="width: 700px;min-height:200px" />
</div>
</template>

<script>
  import CommonWrapper from '@/components/commonWrapper'
  import ECharts from 'vue-echarts/components/ECharts'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title'

  export default {
    name: 'charts',
    data () {
      return {
        option: {
          tooltip: {
              trigger: 'axis',
              axisPointer: {
                  type: 'cross',
                  crossStyle: {
                      color: '#999'
                  }
              }
          },
          toolbox: {
              feature: {
                  dataView: {show: true, readOnly: false},
                  magicType: {show: true, type: ['line', 'bar']},
                  restore: {show: true},
                  saveAsImage: {show: true}
              }
          },
          legend: {
              data:['库位面积使用情况','库位标准']
          },
          xAxis: [
              {
                  type: 'category',
                  data: [],
                  axisPointer: {
                      type: 'shadow'
                  }
              }
          ],
          yAxis: [
              {
                type: 'value',
                name: '库位面积使用情况',
                // min: 0,
                // max: 250,
                // interval: 50,
                },
                {
                    type: 'value',
                    name: '库位标准',
                    // min: 0,
                    // max: 25,
                    // interval: 5,
                }
          ],
          series: [],
            animationDuration: 2000
        }
      }
    },
    props: {
      chartData: {
        type: Array
      },
      xAxisData: {
        type: Array
      }
    },
    watch: {
      chartData(newVal) {
        this.option.series = newVal
      },
      xAxisData(newVal) {
        console.log(newVal)
        this.option.xAxis[0].data = newVal;
      }
    },
    components: { CommonWrapper, chart: ECharts }
  }
</script>

<style scoped>
.chart{
  margin:0 auto;
}
.title{
  margin-bottom: 10px;
  text-align: center;
}
.mainTitle{
  font-size:16px;
  font-weight: bold;
}
.subTitle{
  font-size:14px;
  color:#999;
}
</style>



